<template>
    <HeaderBottom :style="{ opacity : `${1 - opacity}` , transform: `translateY(${translate}px)`}" />
    <div class="content-wrapper">
       <div class="content">
           <div class="transaction-overview">
                <div class="title">
                    <h4>Transaction Overview</h4>
                    <router-link to="/transaction">See more</router-link>
                </div>
                <div class="graphic">
                    <Chart />
                </div>
            </div>
            <div class="transaction-overview">
                <div class="title">
                    <h4>Stocks Overview</h4>
                    <a href="#">See more</a>
                </div>
                <div class="graphic">
                    <Chart />
                </div>
                
            </div>
            <div class="transaction-overview">
                <div class="title">
                    <h4>Balance Overview</h4>
                    <a href="#">See more</a>
                </div>
                <div class="graphic">
                    <Chart />
                </div>
            </div>
       </div>
    </div>
</template>

<script>
import HeaderBottom from '@/components/HeaderBottom.vue'
import Chart from '@/components/Chart.vue'

export default {
    components : { HeaderBottom, Chart },
    data() {
        return {
            opacity : 0,
            translate : 0
        }
    },
    created () {
        window.addEventListener('scroll', this.onScroll);
    },
    destroyed () {
        window.removeEventListener('scroll', this.onScroll);
    },
    methods: {
        onScroll () {
            var position = window.pageYOffset
            var position2 = window.pageYOffset
            position /= 10
            position2 /= 100
            this.translate = position
            this.opacity = position2
            if (this.opacity > 1) {
                this.opacity = 1
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/modules/views/home.module.scss';
    @import '@/assets/scss/global.scss';
</style>