<template>
    <div class="content-wrapper">
       <div class="content">
            <div v-show="this.$route.name === 'Transaction'" class="tabs">
                 <div class="buttons">
                    <span class="active" :class="{ slide : toggleTransaction }"></span>
                    <button @click="toggleTransaction = false">Sellings</button>
                    <button @click="toggleTransaction = true">Purchase</button>
                </div>
            </div>
           <Sellings v-if="!toggleTransaction" />
           <Purchase v-else />
       </div>
    </div>
</template>

<script>
import Purchase from './transaction/Purchase.vue'
import Sellings from './transaction/Sellings.vue'
export default {
    components : { Sellings, Purchase },
    data() {
        return {
            toggleTransaction : false
        }
    }
}
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/modules/views/transaction.module.scss';
    @import '@/assets/scss/global.scss';
</style>