import { defineStore } from "pinia";

const useInputStore = defineStore("inputStore", {
    state: () => {
        return {
            isFocus : true,
        }
    },
    actions: {
        hideNavbar() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                setTimeout(() => {
                    this.isFocus = false
                }, 131); 
            }
        },
        showNavbar() {
            if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
                setTimeout(() => {
                    this.isFocus = true
                    
                }, 200);
            };
        }
    }
})

const useProfileMenusStore = defineStore("profileMenus", {
    state: () => {
        return {
            isOpenMenu : false
        }
    }
})

const useNotificationDropmenuStore = defineStore("notificationDropmenu", {
    state: () => {
        return {
            isOpenMenu : false
        }
    }
})

export { useInputStore, useProfileMenusStore, useNotificationDropmenuStore }