<template>
    <div class="header-top-wrapper">
        <div class="header-top">
            <img class="avatar" :src="avatar" @click="this.profileMenusStore.isOpenMenu = true" />
            <h5 v-show="this.$route.name !== 'Home'">Superadmin</h5>
            <button class="notification" v-show="this.$route.name !== 'Notification'" @click="this.notificationDropmenuStore.isOpenMenu = true" >
                <svg class="notif-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 17h5l-1.405-1.405A2.032 2.032 0 0118 14.158V11a6.002 6.002 0 00-4-5.659V5a2 2 0 10-4 0v.341C7.67 6.165 6 8.388 6 11v3.159c0 .538-.214 1.055-.595 1.436L4 17h5m6 0v1a3 3 0 11-6 0v-1m6 0H9"></path>
                </svg>
                <span v-show="notif !== 0">
                    <span v-if="notif <= 99" class="notif-badge">{{ notif }}</span>
                    <span v-else class="notif-badge">99+</span>
                </span>
            </button>
        </div>
    </div>
    <transition name="slide-fade">
        <ProfileMenus v-show="this.profileMenusStore.isOpenMenu"/>
    </transition>
    <transition name="scale-fade">
        <NotificationDropmenu v-show="this.notificationDropmenuStore.isOpenMenu"/>
    </transition>
</template>

<script>
// assets
import avatar from '@/assets/img/avatar-cute.gif'

// components
import ProfileMenus from './profileMenus.vue'
import NotificationDropmenu from './NotificationDropmenu.vue'

// store
import { useProfileMenusStore } from '@/stores/store'
import { useNotificationDropmenuStore } from '@/stores/store'

export default {
    components : { ProfileMenus, NotificationDropmenu },
    data() {
        return {
            avatar : avatar,
            notif : 0,
        }
    },
    setup() {
        const profileMenusStore = useProfileMenusStore()
        const notificationDropmenuStore = useNotificationDropmenuStore()
        return { profileMenusStore, notificationDropmenuStore }
    }
}
</script>

<style lang="scss" scoped>
    @import '@/assets/scss/modules/components/header.module.scss';

    .slide-fade-enter-active { transition: all 0.2s ease-out }
    .slide-fade-leave-active { transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1) }

    .slide-fade-enter-from,
    .slide-fade-leave-to {
        transform: translateY(200px);
        opacity: 0;
    }

    .scale-fade-enter-active,
    .scale-fade-leave-active {
        transition: all 0.1s ease;
        transform-origin: top right;
    }

    .scale-fade-enter-from {
        opacity: 0;
        transform: scale(0.8);
    }
    .scale-fade-leave-to { opacity: 0 }
</style>